*,
*::before,
*::after {
  box-sizing: inherit;
  border-radius: 0;
}

html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  line-height: 1.2;
  background-color: var(--main-bg-color);
  overflow-x: hidden !important;
}
body.body-locked {
  overflow: hidden;
}

footer {
  margin-top: auto;
}

/* img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
} */
a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.section {
  padding: 100px 0;
  position: relative;
  background-color: var(--main-bg-color);
  z-index: 1;
  outline: 1px solid red;
}

.main-title {
  font-weight: 600;
  font-size: 36px;
  color: var(--primary-color);
}

.section-mb {
  margin-bottom: 20px;
}

.section-padding {
  padding-top: 100px;
}
@media (max-width: 1440px) {
  .section-padding {
    padding-top: 60px;
  }
}
@media (max-width: 1140px) {
  .section-padding {
    padding-top: 20px;
  }
}

.fieldset-default {
  margin: 0;
  padding: 0;
  border: 0;
}

.text-nowrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.button-default {
  border: 0;
  margin: 0;
  padding: 0;
  background-color: transparent;
}

.flex-centered-content, .hero__preview, .hero .scroll-btn, .nav__link, .main-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}

.main-scroll::-webkit-scrollbar, ::-webkit-scrollbar {
  background-color: var(--main-scrollbar-color);
  width: var(--main-scrollbar-width);
}
.main-scroll::-webkit-scrollbar-thumb, ::-webkit-scrollbar-thumb {
  background-color: var(--main-scrollbar-thumb-color);
}

.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.overflow-hidden {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #ffffff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-fluid-width);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.hover-opacity, .main-btn {
  transition: opacity var(--main-transition);
}
.hover-opacity:hover, .main-btn:hover, .hover-opacity:focus, .main-btn:focus {
  opacity: 0.7;
}

.main-btn {
  font-family: "Open-Sans";
  width: 100%;
  max-width: fit-content;
  min-height: 55px;
  padding: 15px;
  color: var(--white-color);
  background-color: var(--gray-color);
  border: 1px solid transparent;
}
@media (max-width: 600px) {
  .main-btn {
    min-height: 40px;
    padding: 10px;
  }
}

.background-color-hover {
  transition: background-color var(--main-transition);
}
.background-color-hover:hover, .background-color-hover:focus {
  opacity: 1;
  background-color: var(--hover-black);
}

.background-white-hover {
  transition: background-color var(--main-transition), color var(--main-transition);
}
.background-white-hover:hover, .background-white-hover:focus {
  opacity: 1;
  color: var(--gray-color);
  background-color: var(--white-color);
}

/* Компоненты */
.main-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 99;
  background-color: var(--main-bg-color);
}
.main-header #logo {
  width: 150px;
  height: 100px;
}
.main-header__small-logo {
  position: relative;
  margin-right: 30px;
  z-index: 99;
  /* opacity: 0;
  z-index: -1; */
}
.main-header__navbar {
  display: flex;
  align-items: center;
  padding: 10px 0;
  overflow-x: hidden;
}
.main-header__navbar .nav {
  flex-grow: 1;
}
@media (max-width: 960px) {
  .main-header__navbar {
    justify-content: space-between;
  }
}

.nav {
  position: relative;
}
@media (max-width: 960px) {
  .nav {
    display: none;
    height: 100vh;
    width: calc(100% - var(--container-offset) * 2);
    max-height: 100vh;
    position: absolute;
    top: 0;
    background-color: var(--main-bg-color);
    padding-top: 70px;
  }
  .nav.mobile-opened {
    display: block;
  }
}
.nav__underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  content: "";
  background-color: var(--primary-color);
  transform: translateX(100%);
}
@media (max-width: 960px) {
  .nav__underline {
    display: none;
  }
}
.nav__list {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: -38px;
  z-index: -1;
}
@media (max-width: 960px) {
  .nav__list {
    margin-top: 0;
    flex-direction: column;
    height: 100%;
    justify-content: center;
  }
}
.nav__list.opened {
  z-index: 1;
}
.nav__item {
  flex-grow: 1;
  opacity: 0;
  transform: translateY(-250px);
}
@media (max-width: 960px) {
  .nav__item {
    flex-grow: initial;
  }
}
.nav__link {
  padding: 8px;
  font-size: 18px;
  height: 40px;
  color: var(--primary-color);
  transition: color var(--transition), transform var(--transition);
}
.nav__link:hover, .nav__link:focus {
  color: var(--dark-color);
  transform: translateY(-5px);
}
@media (max-width: 1140px) {
  .nav__link {
    font-size: 16px;
  }
}

.burger {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 65px;
  height: 40px;
  padding: 0.5rem 1rem;
  border-radius: 30px;
  border: 1px solid transparent;
  color: var(--primary-color);
  background-color: var(--main-bg-color);
  z-index: 99;
}
.burger__line {
  width: 30px;
  height: 2px;
  transition: width 0.3s ease-in;
  background-color: var(--primary-color);
}
@media (max-width: 960px) {
  .burger__line--middle {
    display: none;
  }
}

/* Секции */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  background-color: var(--main-bg-color);
  z-index: 1000;
}

.hero {
  position: fixed;
  width: 100vw;
  height: 100vh;
  min-height: 860px;
  max-height: 1080px;
  display: flex;
  padding-top: 100px;
  padding-bottom: 20px;
}
@media (max-width: 960px) {
  .hero {
    min-height: 684px;
    padding-top: 80px;
  }
}
.hero__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}
