.hero .scroll-btn svg {
  animation: moving 1.5s linear infinite;
}
@keyframes moving {
  0% {
    opacity: 0;
    transform: translateY(-15px);
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(15px);
  }
}
.hero__preview {
  height: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  z-index: 2;
  margin-bottom: 10vh;
}
.hero__preview svg {
  width: calc(85vw - 300px);
  height: 75vh;
  min-width: 300px;
  min-height: 250px;
  max-height: 50vh;
}
.hero__preview svg g[data-id=house] {
  opacity: 0;
  transform: translateY(150px);
}
.hero__preview svg path[data-id=house-window] {
  opacity: 0;
}
.hero__preview svg g[data-id=name-container] {
  opacity: 0;
}
.hero__preview svg g[data-id=name-container] path {
  transform: translateY(-15px);
}
@media (max-width: 1140px) {
  .hero__preview svg {
    width: 85vw;
  }
}

.portfolio {
  position: relative;
  height: 100vh;
  margin-top: 100vh;
  background-color: var(--main-bg-color);
  z-index: 1;
}

.accordion {
  position: relative;
  overflow: hidden;
  min-height: 800px;
}
.accordion__field {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--main-bg-color);
}
.accordion__field:not(:last-child) .accordion__header {
  border-right: 0;
}
.accordion__header {
  position: relative;
  width: 100%;
  min-width: 70px;
  max-width: 70px;
  height: 600px;
  padding: 20px;
  border: 1px solid var(--primary-color);
  background-color: var(--white-color);
  z-index: 3;
  cursor: pointer;
}
.accordion__header span {
  font-weight: 500;
  font-size: 20px;
  color: var(--primary-color);
  text-align: center;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-90deg);
}
.accordion__body {
  display: flex;
  align-items: center;
  border: 1px solid var(--primary-color);
  border-left: 0;
  padding: 30px;
}
.accordion__body img {
  margin-right: 30px;
}

