*,
*::before,
*::after {
  box-sizing: inherit;
  border-radius: 0;

  @extend .main-scroll;
}

html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  line-height: 1.2;
  background-color: var(--main-bg-color);
  overflow-x: hidden !important;

  &.body-locked {
    overflow: hidden;
  }
}

footer {
  margin-top: auto;
}

/* img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
} */

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.section {
  padding: 100px 0;
  position: relative;
  background-color: var(--main-bg-color);
  z-index: 1;
  outline: 1px solid red;
}

.main-title {
  font-weight: 600;
  font-size: 36px;

  color: var(--primary-color);
}
